.contact {
  min-height: 120vh;
  width: 100vw;
  text-align: center;
  padding-bottom: 2rem;
}
.contact .row {
  display: flex;
  align-items: center;
  justify-content: center;
}
.contact .row .image img {
  height: 70vh;
  width: 50vw;
}
.contact .row .form-container {
  width: 50%;
  text-align: left;
  padding: 0 5rem;
}
.contact .row .form-container input, .contact .row .form-container textarea {
  outline: none;
  border: none;
  height: 4rem;
  background: none;
  border-radius: 0.5rem;
  box-shadow: 0.2rem 0.2rem 0.5rem rgba(0, 0, 0, 0.2);
  padding: 0 1rem;
  margin: 1rem 0;
  font-size: 1.6rem;
}
.contact .row .form-container .inputBox {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.contact .row .form-container .inputBox input[type=text] {
  width: 49%;
}

.contact .row .form-container input[type=email] {
  width: 100%;
}

.contact .row .form-container textarea {
  width: 100%;
  height: 20rem;
  padding: 1rem;
  resize: none;
}

.contact .row .form-container input[type=submit] {
  width: 10rem;
  height: 3rem;
  width: 10rem;
  background: #fff;
  color: #444;
  cursor: pointer;
  border: none;
  outline: none;
  margin-top: 2rem;
  font-weight: 500;
  box-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, 0.3);
  border-radius: 30px;
  transition: 2s;
}

.contact .row .form-container input[type=submit]:hover {
  display: inline-block;
  align-items: center;
  justify-content: center;
  border-radius: 0px;
  color: var(--first-color);
  padding: 10px 40px;
  border: 0px;
  box-shadow: 6px 6px 0px 0px var(--first-color), 0 0 8px var(--second-color);
}

.contact .row .form-container input[type=submit]:hover {
  opacity: 0.8;
}

@media (max-width: 768px) {
  .contact .row {
    flex-flow: column;
    width: auto;
  }
  .contact .row .image img {
    width: 100vw;
  }
  .contact .row .form-container {
    width: 100%;
    padding: 0 1.5rem;
  }
}/*# sourceMappingURL=contact.css.map */