.contact {
    min-height: 120vh;
    width: 100vw;
    text-align: center;
    padding-bottom: 2rem;
    .row {
        display: flex;
        align-items: center;
        justify-content: center;
        .image img {
            height: 70vh;
            width: 50vw;
        }
        .form-container {
            width: 50%;
            text-align: left;
            padding: 0 5rem;
            input, textarea {
                outline: none;
                border: none;
                height: 4rem;
                background: none;
                border-radius: .5rem;
                box-shadow: .2rem .2rem .5rem rgba(0, 0, 0, .2);
                padding: 0 1rem;
                margin: 1rem 0;
                font-size: 1.6rem;
            }
            .inputBox {
                width: 100%;
                display: flex;
                justify-content: space-between;
            }
        }
    }
}
.contact .row .form-container .inputBox input[type="text"] {
    width: 49%;
}
.contact .row .form-container input[type="email"] {
    width: 100%;
}
.contact .row .form-container textarea {
    width: 100%;
    height: 20rem;
    padding: 1rem;
    resize: none;
}
.contact .row .form-container input[type="submit"] {
    width: 10rem;
    height: 3rem;
    width: 10rem;
    background: #fff;
    color: #444;
    cursor: pointer;
    border: none;
    outline: none;
    margin-top: 2rem;
    font-weight: 500;
    box-shadow: 0 .3rem .5rem rgba(0, 0, 0, .3);
    border-radius: 30px;
    transition: 2s;
}
.contact .row .form-container input[type="submit"]:hover {
    display: inline-block;
    align-items: center;
    justify-content: center;
    border-radius: 0px;
    color: var(--first-color);
    padding: 10px 40px;
    border: 0px;
    box-shadow: 6px 6px 0px 0px var(--first-color), 0 0 8px var(--second-color);
}
.contact .row .form-container input[type="submit"]:hover {
    opacity: .8;
}

@media (max-width: 768px) {
    .contact .row{
        flex-flow: column;
        width: auto;
        .image img {
            width: 100vw;
        }
        .form-container {
            width: 100%;
            padding:0 1.5rem;
        }
    }
}